/* Dashboard Layout */
.dashboard-container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 16px;
  }
  
  /* Chart Card Styling */
  .chart-card {
    margin-bottom: 24px;
    border-radius: 12px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  /* Tab Styling */
  .tabs-list {
    display: flex;
    gap: 12px;
    margin-bottom: 16px;
  }
  
  .tab-trigger {
    padding: 12px 20px;
    font-size: 18px;
    font-weight: 600;
    background-color: #edf2f7;
    border: 1px solid #cbd5e0;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .tab-trigger[data-state='active'] {
    background-color: #3182CE;
    color: white;
  }
  
  .tab-trigger:hover {
    background-color: #63B3ED;
  }
  
  /* Dropdown Styling */
  .select-trigger {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 12px;
    border: 1px solid #cbd5e0;
    border-radius: 8px;
    background-color: white;
    cursor: pointer;
    font-size: 16px;
  }
  
  .select-content {
    margin-top: 4px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    background-color: white;
    overflow: hidden;
  }
  
  .select-item {
    padding: 12px;
    cursor: pointer;
    transition: background-color 0.2s;
  }
  
  .select-item:hover {
    background-color: #f1f5f9;
  }
  
  /* Tooltip Styling */
  .recharts-tooltip-wrapper {
    background-color: white;
    border: 1px solid #e2e8f0;
    padding: 8px;
    border-radius: 8px;
  }
  
  /* Responsive Styles */
  @media (max-width: 768px) {
    .tab-trigger {
      font-size: 16px;
      padding: 10px;
    }
  }
  